@import 'src/util/mixinFn';

/* Resetting All the Default Settings */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Overall Settings */
body {
    font-family: 'Roboto', sans-serif;
    line-height: 1.4;
}

a {
    text-decoration: none;
}

p {
    margin: 0.75rem 0;
}

i {
    margin-right: 5px;
}

.app {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;

    &__content {
        @include flex(row);
    }

    &__container {
        @include w-h(65%, auto);
    }
}